@import "../../global.scss";

.contenedor{
    
   width: 100%;
    position:fixed ;
    background-image: url("../../rec.png");
    background-repeat: no-repeat;
    background-position: center;
    background-color: #ffff;
    box-shadow: 2px 2px 40px 15px rgba(0, 0, 0, 0.4);
    z-index: 5;
    border-bottom: 3px solid #fbba00;
    
    
    .cajas{
       
        padding: 0px 70px;
        justify-content:space-between;
        align-items: center;
        display: flex;
        @include mobile{
            padding:0;
        }
        
        .caja__logo{
            padding:25px 20px 10px 10px;

            @include mobile{
                padding: 0;
            }
            img{
                cursor: pointer;
                @include mobile{
                    transform: scale(0.75);
                    padding-top: 6px;
                }
            }
            }
        .caja__menu{
                 
                padding: 20px;
                @include mobile{
                    display: none;
                }
            ul{
                
                display: flex;
                list-style: none;
                li{
                    
                    font-size: 1.5rem;
                    font-family: BebasBold;
                    padding: 0px 15px;
                    a{
                        color: rgb(23, 23, 23);
                        text-decoration: none;
                        transition: all 0.5s ease;
                        &:hover{
                            color: #fbba00;
                            letter-spacing: 6px;
                        }
                        
                    }
                }
            }
            
        }
    }
}