@import "../../global.scss";

.portfolio{
    
    background-color: #fff;
    
    background-repeat: no-repeat;
    background-position: top ;
    // background-color:antiquewhite;
    display: flex;
    flex-direction: column;
    align-items: center;
    
    @include desktop{
        margin-top: 120px;
    }
    @include mobile{
        background-size: 40rem;
        margin-top: 70px;
    }
    
    

    .portfolio__title{
        margin-top: 10px;
        margin-bottom: 40px;
        text-align: center;
        //  text-align: center;
        .title{
             font-family: BebasBold;
             font-size: 3.7rem;
             padding: 60px 0 30px 0;
             letter-spacing: 15px;
             @include mobile{
                 letter-spacing: 10px;
                 font-size: 2.9rem;
                 padding: 40px 0 10px 0;

             }
        }
        .subtitle{
            font-family: PoppinsLight;
            letter-spacing: 4px;
            font-size: 1rem;
            margin-top: 15px;
            color: #fbba00;

            @include mobile{
                margin: 0;
                color: #999999;
                letter-spacing:2px;
            }
         }
        .guion{
            margin-top:50px;
        }
    }
    .catt{
        background-color:red;
    }
    

 

 
}