
@import "../../global.scss";

.portafolio{
  
    .piercings {
        // background-repeat: no-repeat;
        
        height: 100%;
        width: 100%;
        background-image: url(./../../unsplash.png);
        background-size: cover;
        background-position: right;
            @include mobile{
                background-position: right;
                background-size: 75rem;
                background-repeat: no-repeat;
                text-align: center;
                
            }

        .piercings__texto {
            
            width: 100%;
            padding : 80px 340px 70px 200px;
            
                @include mobile{
                    padding:30px 2px;
                }
                img{
                    transform: scale(1.1);
                    
                }
                @include mobile{
                    transform: scale(0.8)
                }

            .piercings__botones {
                padding-top: 50px;

                button {
                    margin-right: 20px;
                    cursor: pointer;
                    background-color: #F7F8FC;
                    border: none;
                    box-shadow: 2px 2px 20px rgba(36, 36, 36, 0.589);
                    padding: 12px 35px;
                    text-align: center;
                    text-decoration: none;
                    font-family: BebasBold;
                    color: DarkGris;
                    font-size: 1.2rem;
                    transition: all 0.6s ease;

                    &:hover{
                        background-color: #fbba00;
                        color: #fff;
                        letter-spacing: 3px;
                    }
                        @include mobile{
                            font-size: 1.7rem;
                            letter-spacing:1px;
                        }
                }
            }
        }

        // height: 438px;
        // background-image: url(./../img/pier.png);
        // background-size: left;
    }
    .contacto {
        .primero {
            background-color: #131414;



            .datos {
                padding: 40px 0px 70px 0px;
                margin: 0 auto;


                display: flex;
                max-width: 1240px;
                justify-content: space-around;
                @include mobile{
                    flex-direction: column;
                    padding: 0px;
                }

                .dato {
                    text-align: center;

                    @include mobile{
                        padding: 0px 0px 20px 0px;
                        border-bottom: solid 1px #989898 ;
                    }

                    img {
                        transform: scale(0.4);
                        transition: all 0.9s ease-in-out;
                        &:hover{
                            transform:scale(0.6);
                        }
                    }

                    p {
                        font-family: PoppinsLight;
                        
                        padding-bottom: 10px;
                        color: #ffff;

                        @include mobile{
                            padding: 10px 15px;
                            margin-top: -25px;
                        }
                        
                    }

                    span {
                        font-family: BebasBold;
                        color: #fbba00;

                      }
                       
                    }
                }
            }

            .redes__sociales {
                background-color: #fff;
                padding: 40px 0 40px 0;

                .red__social {
                    display: flex;
                    list-style: none;
                    margin: 0 auto;

                    display: flex;
                    max-width: 340px;
                    justify-content: space-around;

                    .redes__icons {
                        transform: scale(0.7);
                    }
                }
            }

        }
     }

