@import "../../global.scss";

.artist{
    position: relative;
    background-image: url(../../../public/static/media/backdark.png);
    background-repeat: no-repeat;
    
    margin-top: -10px;
    
    padding-bottom: 250px;
    height: 100%;

        @include desktop{
            height: 100vh;
        }

        @include mobile{
            background-size: cover;
            height: 100%;
            padding-bottom: 100px;
            
        }
    
   
   .artist__title{
       margin-top: 10px;
       margin-bottom: 10px;
        text-align: center;
        color: #fbba00;
        @include mobile{
            
        }
       .title{
            font-family: BebasBold;
            font-size: 3.7rem;
            padding: 180px 0 30px 0;
            letter-spacing: 15px;
                @include desktop{
                    padding:  60px 0 30px 0;
                }

                @include mobile{
                    padding: 60px 0 10px 0;
                    font-size: 3.2rem;
                }
       }
       .guion{
           margin-top:50px;
           @include mobile{
               transform: scale(0.7);
               margin: 0;
           }
       }
   }
   .artist__banner {
        
        
        height: 50vh;
        width: 70%;
        display: flex;
        justify-content: space-around;
        margin: 0 auto;

        @include mobile{
            height: 100%;
            width: 100%;
            flex-direction: column;
          
        }


    

    .banner__hijo {

        // background-color: #F7F8FC;
        flex: 2;

        .figura {
            
            width: 100%;
           
            background-repeat: no-repeat;
            background-position: center;
            @include mobile{
                
                background-repeat: no-repeat;
            }
        }







        .nombre {
            padding: 20px 0px 0px 0px;
            @include mobile{
                padding: 0;
            }

            .nombre__titulo {
               padding-left: 50px;
                
               

                img {

                    margin: 0;
                    transform: scale(0.8);
                }
            }

            .last {
                padding-left: 110px;
                @include mobile{
                    
                    padding: 0px 34px;
                }

                p {
                    font-family: PoppinsLight;
                    font-size: 1rem;
                    opacity: 0.9;
                    color: #fff;

                    @include mobile{
                        font-size: 0.9rem;
                        line-height: 1.5rem;
                    }
                }

                .boton__portafolio {
                    
                    padding-top: 60px;
                    

                    @include mobile{
                        padding-top:25px;
                        z-index:-100;
                    }

                    

                    button {
                        cursor: pointer;
                        transition: all 0.5s ease;
                        background-color: #fbba00;
                        border: none;
                        padding: 12px 35px;
                        text-align: center;
                        text-decoration: none;
                        font-family: BebasBold;
                       
                        color: $fuckgrey;
                        font-size: 1.2rem;
                        letter-spacing: 1.3px;
                        &:hover{
                            background-color: #fbba00;
                            color: #fff;
                        }

                    }
                }
            }
        }
    }
}
.maquina{
    position: absolute;
    right: 90px;
    bottom: -200px;

    @include desktop{
        right: 0px;
    }

    @include mobile{
        bottom: -120px;
        
        
    }
    
}
}
