@import "../global.scss";

.list-group{
    
    width: 100%;
    background-color:#0E0F11;
    list-style: none;
    display: flex; 
    justify-content: center;
    z-index: 2;

    @include mobile{
        display: inline;
        text-align: center;
        
    }
    
    
    .list-group-item{
        color: #fff;
        font-size: 1.2rem;
        
        padding: 17px 40px;
        font-family: PoppinsLight;
        cursor: pointer;  
        
        @include mobile{
            font-size: 1rem;
            margin: 0;
            padding: 17px 20px;
            border-bottom: solid 1px #fbba00;
        }

        &:hover{
            background-color: #fbba00;
            color: #fff;
        }
       
    }

}